.main-container {
	display:block;
  max-width: 1200px;
    width: 100%;
    margin-right: auto;
    margin-left: 20px;
/*    margin-top: 150px;*/
}

h2 {
	font-weight: 800;
}

.writeline {
  display:block;
	position:relative;
    z-index: 11;
    /*padding-top: 25px;*/
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.67;
    letter-spacing: normal;
}

.rectangle
{
	padding: 20px;
	z-index: 10;
	position: absolute;
	width:160%;
/*	height: 100%;*/
	border-radius: 8px;
	border: solid 6px #FFD01E;
	background-color: #FFFFFF;

}

form.w-form {
    max-width: 50%;
    margin: auto;
    padding: 20px;
    background: whitesmoke;
    border-radius: 10px;
    float: right;
    display: block;
    z-index: 11;
    margin-left: 20px;
    margin-bottom: 30px;
}

.intro-para {
	float:right;
	text-align: justify;
	font-size: 16px;
	line-height: 22px;
	font-weight: 500;
	margin-top: 5px;
	margin-bottom: 10px;
}

h4 {
	font-size: 30px;
    line-height: 30px;
    font-weight: 800;
}

.td-letter {
	font-size: 16px;
	line-height: 24px;
}

.td-letter span {
   content: "\A";
  white-space: pre;
}

.formblock {
	padding:10px;
	max-width: 100%;
	margin:auto;
	background-color: transparent;
}

.text-field {
	display:block;
	min-height: 50px;
	margin-top: -1px;
	margin-bottom: 0px;
	padding-top:15px;
	padding-bottom: 15px;
	border-width: 1px;
	border-color: #DBDBDB;
/*	color:#FFD01E;*/
	font-size:21px;
	line-height: 26px;
	font-weight: 700;
}

dt {
/* float:left;*/
}

label {
	font-size: 18px;
	color:black;
	display: block;
    margin-bottom: 5px;
    font-weight: bold;
}

label a {
	font-size:14px;
}

input {
	line-height:normal;
	width: 100%;
	margin-left:-40px;
}

input[type="text"] {
	border: none;
    border-bottom: solid 1px black;
    color: black;
    background-color: transparent;
    margin-left:-40px;
}

select {
/*	display:flex;*/
  	flex-direction: column;
  	/*position:relative;*/
  	width:200px;
  	height:100%;
	margin-left:-40px;
	border-radius:4px;
	border:1px solid;
	font-size: 18px;
    outline-color: #ffad12;
    outline-width: thick;
}


option {
  padding:0 30px 0 10px;
  min-height:40px;
  display:flex;
  align-items:center;
  background:  #E5E5E5;
  border-top:#222 solid 1px;
  position:absolute;
  top:0;
  width: 100%;
  pointer-events:none;
  order:2;
  z-index:1;
  transition:background .4s ease-in-out;
  box-sizing:border-box;
  overflow:hidden;
  white-space:nowrap;
}


.legal 
{
	text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
    font-size: 14px;
    line-height: 18px;
}

.submit {
	  width:200px;
    display: block;
 /*   position: relative;*/
    margin-left: auto;
    margin-right: auto;
    padding-top: 15px;
    padding-bottom: 15px;
    border-radius: 5px;
    background-color: #ffd01e;
    box-shadow: 0 20px 20px -12px ;
    transition: box-shadow 200ms ease, background-color 200ms ease;
    font-weight: 700;
/*    font-size: 18px;*/
    text-align: center;
    color: black;
    font-style: normal;
}



